* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#clock {
  position: relative;
  margin: auto;
  width: 445px;
  display: flex;
  height: 400px;
  overflow: visible;
}
.set-time {
  display: block;
  cursor: pointer;
}

#first-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 0;
  margin-top: 50px;
}
#break-length {
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 110px;
  margin-left: 120px;

  line-height: 1em;
}
#session-length {
  position: absolute;
  top: 0;
  right: 0;

  margin-top: 110px;
  margin-right: 120px;
  line-height: 1em;
}
#timer-label {
  margin-top: 5px;
  line-height: 1em;
}

#time-left {
  position: absolute;
  top: 0;
  margin-top: 110px;
  width: 20px;
  height: 2em;
  margin-left: 200px;
  line-height: 1em;
}
#start_stop {
  width: 50px;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 140px;
  margin-left: 170px;
  cursor: pointer;
}
#reset {
  width: 50px;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 140px;
  margin-left: 220px;
  cursor: pointer;
}
#break-section {
  position: absolute;
  left: 0;
  margin-top: 25px;
  margin-left: 40px;
}
#session-section {
  position: absolute;
  right: 0;
  margin-top: 25px;
  margin-right: 40px;
}

#session-label {
  margin-right: 12px;
  line-height: 1em;
}
#break-label {
  margin-left: 20px;
  line-height: 1em;
}

@media (max-width: 500px) {
  #clock {
    width: 375px;
  }
  #time-left {
    margin-left: 165px;
  }
  #start_stop {
    margin-left: 135px;
  }
  #reset {
    margin-left: 185px;
  }
}
